@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Oswald:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#bio, .portfolio {
    margin-left: 75px;
    margin-right: 75px;
    padding-top: 20px;
    padding-bottom: 20px;
}

.background {
    background-color: #d8c7b2;
    border-top: solid 5px #aa231e;
    border-bottom: solid 5px #aa231e;
}

#bio-text > p, h2 {
    color: black;
}

#portrait {
    border: solid #aa231e 5px;
    border-radius: 1000px;
}

#bio-header {
    color: black;
}
img {
    width: 100%;
    min-width: 250px;
}

.project-title {
    color: black;
    font-family: 'Oswald', sans-serif;
}
.portfolio {
    margin-left: 75px;
    margin-right: 75px;
}

.portfolio-background {
    background-color: #221401;
}

h2 {
    font-family:  'Oswald', sans-serif;
    color: white;
}
.jumbo {
    position: relative;
    text-align: center;
    color: white;
}

h1 {
    position: absolute;
    top: 35%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    font-family: 'Oswald', sans-serif;
    color: #f3f3f3;
}

#jumbo-title {
    font-size: 75px;
    text-shadow: -2px 2px #2e2d2d;
}

@media screen and (max-width: 950px) {
    #jumbo-title {
        font-size: 60px;
        text-shadow: -2px 2px #2e2d2d;
    }
  }
@media screen and (max-width: 750px) {
    #jumbo-title {
        font-size: 40px;
        text-shadow: -2px 2px #2e2d2d;
    }
  }
@media screen and (max-width: 550px) {
    #jumbo-title {
        font-size: 30px;
        text-shadow: -2px 2px #2e2d2d;
    }
  }
#contact {
    background-color: #d8c7b2;
    border-top:  #aa231e 5px solid;
    margin-bottom: 0;
    padding-bottom: 20px;
}

.contact {
    padding-left: 75px;
}

#contact-head {
    padding-left: 75px;
    color: black;
}

text {
    color: black;
    font-family: 'Oswald', sans-serif;
}

.info ul li a {
    color: black;
    text-decoration: none;
    font-family: 'Oswald', sans-serif;
    opacity: 100%;
}

li {
    list-style: none;
}
header {
    position: fixed;
    width: 100%;
    top: 0px;
    left: 0px;
    /* color: #9ba1af; */
    z-index: 1;
}

header ul {
    text-align: center;
    list-style-type: none;
}

/* header nav {
    padding: 7px 0 2px 0;
    background-color: hsl(39, 43%, 31%,.7);
} */

li a {
    color: white;
    text-decoration: none;
    font-family: 'Oswald', sans-serif;
    opacity: 100%;
}

li a:hover {
    color: #851713;
    font-weight: bold;
}

header li {
    margin-right: 20px;
}

header nav ul li {
    display: inline-block;
    margin-right: 50px;
}

header h1 {
    margin-left: 20px;
}

html {
    /* The scroll padding works in most browsers, but notably not in Safari or internet explorer, resulting in less than ideal UX in those browsers.*/
    scroll-padding-top: 65px;
}
.footer {
    height: 75px;
    background-color: #aa231e;
    margin-bottom: 0;
    margin-top: 0;
    text-align: center;
    padding-top: 15px;
}
